body {
  background: url(/static/img/big-background.a9839ed.jpg) center 4vw/144vmin no-repeat, url(/static/img/pattern-background.0a6e28c.png) 0/13.55vmin;
}
#argentumland-logo {
  margin: 8vw 0 4vw 0;
  text-align: center;
}
#argentumland-logo img {
  max-width: 100%;
  max-height: 100%;
}

#main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 0 20px 0;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.tooltip {
  color: white;
  font-family: 'Ubuntu Mono', monospace;
}
#main > div {
  margin: 0 20px 0;
}
@media (max-width: 23.9em) {
#main {
    padding: 0;
}
#main > div {
    margin: 0;
}
}
#content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  max-width: 800px;
}

ul#server-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

ul#server-list li {
  margin: 0 0 20px 0;
  height: 160px;
  background: url(data:image/png;base64,);
  position: relative;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 7px;
  color: #aeaeae;
}
/* center block when only showing image */
@media (max-width: 33.9em) {
ul#server-list {
    text-align: center;
}
ul#server-list li a.server-logo {
    max-width: 100%;
}
}
@media (max-width: 23.9em) {
ul#server-list li {
    padding: 0;
    margin: 0;
}
}
/* To make the border above children http://stackoverflow.com/a/15223089 */
@media (min-width: 23.9em) {
ul#server-list li {
    -webkit-box-shadow: 10px 10px 23px -2px rgba(0, 0, 0, 0.75);
            box-shadow: 10px 10px 23px -2px rgba(0, 0, 0, 0.75);
}
ul#server-list li:after {
    content: "";
    /* Expand element */
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    -webkit-box-shadow: inset 0px 0px 2px 2px #000, inset 0px 0px 0px 5px #620, inset 0px 0px 2px 7px #000;
            box-shadow: inset 0px 0px 2px 2px #000, inset 0px 0px 0px 5px #620, inset 0px 0px 2px 7px #000;
    /* Disable click events */
    pointer-events: none;
}
ul#server-list li:first-child:after {
    -webkit-box-shadow: inset 0px 0px 2px 2px #000, inset 0px 0px 0px 5px #ff9901, inset 0px 0px 2px 7px #000;
            box-shadow: inset 0px 0px 2px 2px #000, inset 0px 0px 0px 5px #ff9901, inset 0px 0px 2px 7px #000;
}
ul#server-list li:nth-child(2):after {
    -webkit-box-shadow: inset 0px 0px 2px 2px #000, inset 0px 0px 0px 5px #99b, inset 0px 0px 2px 7px #000;
            box-shadow: inset 0px 0px 2px 2px #000, inset 0px 0px 0px 5px #99b, inset 0px 0px 2px 7px #000;
}
}
ul#server-list li p {
  margin: 0;
  padding: 0 0 3px 3px;
  font-size: 4em;
  color: #fff;
  font-family: "Ubuntu Mono", sans-serif;
  text-shadow: 0 1px 0 #ccc, 0 2px 0 #c9c9c9, 0 3px 0 #bbb, 0 4px 0 #b9b9b9, 0 5px 0 #aaa, 0 6px 1px rgba(0, 0, 0, 0.1), 0 0 5px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3), 0 3px 5px rgba(0, 0, 0, 0.2), 0 5px 10px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.2), 0 20px 20px rgba(0, 0, 0, 0.15);
}
ul#server-list li .overlay {
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: calc(100% - 14px);
  height: 160px;
  color: inherit;
}
ul#server-list li .overlay.ranking {
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  pointer-events: none;
  color: #5b80b2;
}
ul#server-list a.server-logo {
  background: url(/static/img/background.da665af.png) center no-repeat black;
  width: 100%;
  height: 160px;
  min-width: 320px;
  max-width: 320px;
  display: inline-block;
  text-align: center;
}
/* Disable pointer on image when showing full info */
@media (min-width: 33.9em) {
ul#server-list a.server-logo {
    /*pointer-events: none;*/
}
}
@media (max-width: 61.9em) {
ul#server-list a.server-logo {
    min-width: 130px;
}
}
ul#server-list img {
  max-width: 80%;
  max-height: 80%;
}
/* vertical align http://stackoverflow.com/a/7310398 */
ul#server-list a.server-logo:before,
.serverlist_before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
ul#server-list img {
  vertical-align: middle;
}
/* Move this to conditional comments */
ul#server-list a.server-logo {
  list-style: none;
  behavior: expression( function(t){ t.insertAdjacentHTML('afterBegin','<span class="serverlist_before"></span>'); t.runtimeStyle.behavior = 'none'; }(this) );
}
/** end vertical align **/

li > .expand {
  width: 100%;
  height: 100%;
}
.mg-x-axis text {
  font-size: 1.2rem !important;
}
div#chart {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.mg-missing .mg-main-line {
  opacity: .5;
}
.mg-missing .mg-main-area {
  opacity: .15;
}
.mg-missing-pane {
  -webkit-transform: translateX(-28px) translateY(8px) scaleX(1.0208);
          transform: translateX(-28px) translateY(8px) scaleX(1.0208);
}

ul#server-list a {
  color: #fff;
  text-decoration: none;
}
ul#server-list a:hover {
  color: inherit;
}
ul#server-list li .stats {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 20px;
  text-align: left;
  font-family: "Ubuntu Mono", sans-serif;
  min-width: 368px;
}
ul#server-list li .stats div {
  white-space: nowrap;
  overflow: hidden;
}
ul#server-list li .stats div {
  padding: 0 0 0 4px;
}
ul#server-list li .stats .labels {
  margin: 0 10px 0 0;
  font-weight: bold;
}
@media (max-width: 33.9em) {
ul#server-list li .stats {
    display: none;
}
}
@media (max-width: 33.9em) {
ul#server-list li .stats.mini {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    min-width: 0px;
    max-width: 320px;
    font-size: .8em;
}
}
